import { Flex, Form, FormProps, Input, Typography } from "antd";
import { useState } from "react";
import { useNavigate } from "react-router-dom";

import Logo from "@/assets/logo.png";
import { sleep } from "@/utils";

import { StyledButton, StyledCard, StyledSignInContainer, StyledTitle } from "./SignUp.styled";

interface FieldType {
  email: string;
  password: string;
  confirmPassword: string;
  remember: string;
}

export function SignUp() {
  const navigate = useNavigate();
  const [isLoading, setIsLoading] = useState<boolean>(false);

  const onFinish: FormProps<FieldType>["onFinish"] = () => {
    setIsLoading(true);
    sleep(1000).then(() => {
      setIsLoading(false);
      navigate("/");
    });
  };

  return (
    <StyledSignInContainer justify="center" align="center" vertical gap={16.5}>
      <img src={Logo} alt="logo" />
      <StyledCard>
        <StyledTitle level={4}></StyledTitle>
        <Form
          name="basic"
          initialValues={{ remember: true }}
          onFinish={onFinish}
          autoComplete="off"
          layout="vertical"
        >
          <Form.Item<FieldType>
            className="input"
            label=""
            name="email"
            rules={[
              {
                type: "email",
                whitespace: false,
                required: true,
                max: 150,
                message: "   !",
              },
            ]}
          >
            <Input placeholder="qwerty@gmail.com" />
          </Form.Item>
          <Form.Item<FieldType>
            label=""
            name="password"
            rules={[
              {
                whitespace: false,
                max: 150,
                required: true,
                message: "  !",
              },
            ]}
          >
            <Input.Password placeholder="*************" />
          </Form.Item>
          <Form.Item<FieldType>
            label=" "
            name="confirmPassword"
            dependencies={["password"]}
            rules={[
              {
                whitespace: false,
                max: 150,
                required: true,
                message: "  !",
              },
              ({ getFieldValue }) => ({
                validator(_, value) {
                  if (!value || getFieldValue("password") === value) {
                    return Promise.resolve();
                  }
                  return Promise.reject(new Error("  "));
                },
              }),
            ]}
          >
            <Input.Password placeholder="*************" />
          </Form.Item>
          <Flex justify="space-between" align="center">
            <Flex vertical>
              <Typography.Link onClick={() => navigate("/")}></Typography.Link>
            </Flex>
            <Form.Item style={{ margin: 0 }}>
              <StyledButton loading={isLoading} type="primary" htmlType="submit" size="large">
                
              </StyledButton>
            </Form.Item>
          </Flex>
        </Form>
      </StyledCard>
    </StyledSignInContainer>
  );
}
